.driverRoom {
   padding: 0 0 30px 0;
   min-height: 100vh;
   box-sizing: border-box;
   background: #0A0A0A;

   &-header {
      padding: 0 15px;
      box-sizing: border-box;

      background: #FFFFFF;
      box-shadow: 0px 4px 12px rgba(142, 142, 147, 0.25);
      border-radius: 0px 0px 12px 12px;

      &-up {
         padding: 20px 0;
         box-sizing: border-box;
         display: flex;
         align-items: center;
         justify-content: space-between;

         a {
            svg {
               width: 30px;
               height: 30px;
            }
         }

         .burger {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            overflow: hidden;

            &.active {
               span {
                  background: transparent;
                  transition: all ease 0.5s;

                  &::after {
                     transform: translateY(0) rotate(-45deg);
                     transition: all ease 0.5s;

                  }

                  &::before {
                     transform: translateY(0) rotate(45deg);
                     transition: all ease 0.5s;
                  }
               }
            }


            span {
               position: absolute;
               width: 30px;
               height: 3px;
               border-radius: 50px;
               background: #0A0A0A;
               transition: all ease 0.5s;


               &::before {
                  content: "";
                  position: absolute;
                  width: 30px;
                  height: 3px;
                  background: #0A0A0A;
                  border-radius: 50px;
                  transform: translateY(-7px);
                  transition: all ease 0.5s;

               }

               &::after {
                  content: "";
                  position: absolute;
                  width: 30px;
                  height: 3px;
                  background: #0A0A0A;
                  border-radius: 50px;
                  transform: translateY(7px);
                  transition: all ease 0.5s;
               }
            }
         }


         .togle {
            #toggle {
               outline: none;
               width: 40px;
               height: 20px;
               border-radius: 40px;
               position: relative;
               -webkit-appearance: none;
               box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.4);
               background: #0A0A0A;
            }

            #toggle:checked {
               background: #FFB00B;
               box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.4);
            }

            #toggle:before {
               content: "";
               letter-spacing: 1px;
               color: rgba(0, 0, 0, .15);
               text-shadow: 1px 1px 1px rgba(255, 255, 255, 1);
               width: 17px;
               height: 17px;
               top: 1px;
               left: 2px;
               position: absolute;
               border-radius: 30px;
               background: white;
               box-shadow: -2px 2px 8px rgba(0, 0, 0, 0.2),
                  -1px 1px 2px rgba(0, 0, 0, 0.3),
                  inset 1px 1px 1px #ffffff;
               transition: all ease 0.5s;

            }

            #toggle:checked:before {
               left: auto;
               transition: all ease 0.5s;
               right: 2px;
               background: #f1f1f1;
            }





         }
      }

      &-body {
         display: flex;
         flex-direction: column;
         overflow: hidden;
         max-height: 0;
         transition: max-height 0.5s ease-out;
         
         &.active {
            max-height: 500px;
            transition: max-height 0.5s ease-in;
         }

         button {
            margin: 20px 0;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 26px;
            text-align: center;
            letter-spacing: -0.5px;
            color: #0A0A0A;
            background: #FFB00B;
            border-radius: 4px;
            padding: 14px 0;
            width: 100%;
            outline: none;
            border: none;
         }

         .row {
            margin-top: 10px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            input {
               position: relative;
               display: block;
               width: 47%;
               font-style: normal;
               font-weight: 500;
               font-size: 18px;
               line-height: 16px;
               letter-spacing: -0.5px;
               color: rgba(142, 142, 147, 0.8);
               border: 1.8px solid rgba(142, 142, 147, 0.8);
               background: white;
               padding: 10px 15px;
               outline: none;
               border-radius: 5px;
               box-sizing: border-box;

               &::placeholder {
                  color: rgba(142, 142, 147, 0.8);
               }

            }





         }

         .row3 {
            margin-top: 10px;
            width: 100%;

            input {
               width: 100%;
               font-style: normal;
               font-weight: 500;
               font-size: 18px;
               line-height: 16px;
               text-align: center;
               letter-spacing: -0.5px;
               color: rgba(142, 142, 147, 0.8);
               border: 1.8px solid rgba(142, 142, 147, 0.8);
               background: white;
               padding: 10px 15px;
               outline: none;
               border-radius: 5px;
               display: none;
               box-sizing: border-box;
            }
         }
      }
   }


   &-body {
      max-width: 576px;
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 15px;
      box-sizing: border-box;

   }

}