.verifyNext {
   height: 100vh;
   padding:  30px 15px;
   box-sizing: border-box;

   &-header {
      a {
         background: transparent;
         border: none;
         outline: none;

         svg {
            fill: black;
         }

         &:disabled {
            svg {
               fill: transparent;
            }
         }
      }
   }
   &-logo {
      margin-top: 100px;
      

      h1 {
         font-style: normal;
         font-weight: bold;
         font-size: 44px;
         line-height: 56px;
         display: flex;
         align-items: center;
         justify-content: center;
         /* or 127% */

         letter-spacing: -0.5px;

         color: #0A0A0A;

         span {
            color: #FFB00B;
            background: #0A0A0A;
            padding: 10px;
            box-sizing: border-box;
            border-radius: 50%;
         }
      }
   }

   &-form {
      margin-top: 50px;
      display: flex;
      flex-direction: column;

      label {
         font-style: normal;
         font-weight: normal;
         font-size: 14px;
         line-height: 17px;
         letter-spacing: -0.5px;
         color: #8E8E93;
         text-align: center;
      }

      .row {
         margin-top: 15px;
         display: flex;
         align-items: center;
         justify-content: space-around;

         input {
            caret-color: transparent;
            width: 50px;
            height: 50px;
            outline: none;
            border: 3px solid #0A0A0A;
            box-sizing: border-box;
            border-radius: 12px; 
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-align: center;
            letter-spacing: -0.5px;
            color: #0A0A0A;
            &::placeholder {
               color: rgba(142, 142, 147, 0.8);
            }

            &:focus {
               background: #0A0A0A;
               color: #FFB00B;
            }

            &:disabled {
               background: #0A0A0A;
               color: #FFB00B;
            }

            
         }
      }
      
      button {
         text-decoration: none;
         box-sizing: border-box;
         margin-top: 40px;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 26px;
         text-align: center;
         letter-spacing: -0.5px;
         color: #0A0A0A;
         background: #FFB00B;
         box-shadow: 0px 4px 8px rgba(142, 142, 147, 0.25);
         border-radius: 12px;
         padding: 10px 20px;
         width: 100%;
         outline: none;
         border: none;
      }
   }
}