.about {
   padding: 30px 15px;
   min-height: 100vh;
   box-sizing: border-box;

   &-header {
      box-sizing: border-box;

      a {
         background: transparent;
         border: none;
         outline: none;

         svg {
            fill: black;
         }

         &:disabled {
            svg {
               fill: transparent;
            }
         }
      }
   }

   &-logo {
      display: flex;
      justify-content: center;
      margin-top: 50px;

      h1 {
         font-style: normal;
         font-weight: bold;
         font-size: 44px;
         line-height: 56px;
         display: flex;
         align-items: center;
         /* or 127% */

         letter-spacing: -0.5px;

         color: #0A0A0A;

         span {
            color: #FFB00B;
            background: #0A0A0A;
            padding: 10px;
            box-sizing: border-box;
            border-radius: 50%;
         }
      }
   }

   &-info {
      text-align: center;
      margin-top: 20px;

      p {
         margin-top: 20px;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 26px;
         text-align: center;
         letter-spacing: 0.5px;
         color: #0A0A0A;

         span {
            color: #FFB00B;
         }

      }
   }

   &-us {
      margin-top: 30px;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
   }
}