.aboutBlock {
   margin-top: 20px;
   min-width: 200px;
   max-width: 200px;
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   text-decoration: none;
   border: 3px solid #FFB00B;
   box-sizing: border-box;
   border-radius: 6px;
   padding: 20px 10px;
   box-sizing: border-box;

   &-image {

      img {
         width: 150px;
         height: 150px;
         overflow: hidden;
         border: 5px solid #FFB00B;
         border-radius: 50%;
      }
   }

   &-info {
      h3 {
         font-style: normal;
         font-weight: 600;
         font-size: 24px;
         line-height: 29px;
         text-align: center;
         letter-spacing: 0.5px;
         color: #0A0A0A;

      }

      h4 {
         font-style: normal;
         font-weight: normal;
         font-size: 18px;
         line-height: 30px;
         text-align: center;
         letter-spacing: -0.5px;
         color: rgba(142, 142, 147, 0.8);
      }
   }
}