select {
   width: 47%;
   font-style: normal;
   font-weight: 500;
   font-size: 18px;
   line-height: 16px;
   text-align: center;
   letter-spacing: -0.5px;
   color: rgba(142, 142, 147, 0.8);

   border: 1.8px solid rgba(142, 142, 147, 0.8);
   background: white;
   padding: 10px 15px;
   outline: none;
   border-radius: 5px;
   box-sizing: border-box;
   -webkit-appearance: none;
   appearance: none;
}