.settings {
   height: 100vh;
   padding: 30px 15px;
   box-sizing: border-box;
   &-header {
      a {
         background: transparent;
         border: none;
         outline: none;

         svg {
            fill: black;
         }

         &:disabled {
            svg {
               fill: transparent;
            }
         }
      }
   }

   &-logo {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
   }

   &-form {
      margin-top: 50px;
      display: flex;
      flex-direction: column;

      input {
         margin-bottom: 10px;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 21px;
         letter-spacing: -0.5px;
         padding: 17px 20px;
         box-sizing: border-box;
         background: #FFFFFF;
         opacity: 0.8;
         box-shadow: inset 0px 2px 6px rgba(142, 142, 147, 0.5);
         border-radius: 12px;
         border: none;
         outline: none;

         &::placeholder {
            color: rgba(142, 142, 147, 0.8);
         }

      }

      button {
         margin-top: 10px;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 26px;
         text-align: center;
         letter-spacing: -0.5px;
         color: #0A0A0A;
         background: #FFB00B;
         box-shadow: 0px 4px 8px rgba(142, 142, 147, 0.25);
         border-radius: 12px;
         padding: 10px 20px;
         width: 100%;
         outline: none;
         border: none;
      }
   }

   .button-box{
      display: flex;
      align-items: center;
      justify-content: space-between;
         button, a {
            text-decoration: none;
            margin-top: 10px;
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 26px;
            text-align: center;
            letter-spacing: -0.5px;
            color: #FFB00B;
            background: #0A0A0A;
            box-shadow: 0px 4px 8px rgba(142, 142, 147, 0.25);
            border-radius: 12px;
            padding: 10px 20px;
            width: 40%;
            outline: none;
            border: none;
         }
   }
}