.info {
   
   display: flex;
   align-items: center;
   justify-content: center;
   height: 100%;
   flex-direction: column;
   text-align: center;

   &-text {
      margin-top: 30px;
      &-title {
         font-style: normal;
         font-weight: 600;
         font-size: 24px;
         line-height: 29px;
         /* identical to box height */

         text-align: center;
         letter-spacing: -0.5px;

         color: #000000;

      }

      &-sub-title {
         margin-top: 15px;
         font-style: normal;
         font-weight: normal;
         font-size: 18px;
         line-height: 30px;
         padding: 0 25px;
         /* or 167% */

         text-align: center;
         letter-spacing: -0.5px;

         /* dark green */

         color: rgba(142, 142, 147, 0.8);

         
      }
      button {
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 26px;
         text-align: center;
         letter-spacing: -0.5px;
         color: #0A0A0A;
         background: #FFB00B;
         padding: 5px 25px;
         border: none;
         border-radius: 10px;
         margin-top: 20px;
      }
   }
}