.home {
   min-height: 100vh;
   box-sizing: border-box;
   padding: 20px 15px;
   &-header {
      box-sizing: border-box;
      button {
         background: transparent;
         border: none;
         outline: none;
         svg {
            fill: black;
         }

         &:disabled {
            svg {
               fill: transparent;
            }
         }
      }
   }

   &-body {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
      .info {
         width: 0;
         overflow: hidden;
         
         
         &.active {

            width: 100%;
         }
      }
   }

   &-footer {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      box-sizing: border-box;
      padding: 10px 0;
      .round {
         display: block;
         margin: 0 5px;
         
         width: 10px;
         height: 10px;
         background: #0A0A0A;
         border-radius: 50%;
         transition: all ease 0.3s;
         
         &.active {
            background: #FFB00B;
            border-radius: 5px;
            width: 20px;
            transition: all ease 0.3s;
         }
      }
   }


}

.change {
   height: 100vh;
   box-sizing: border-box;
   display: flex;
   align-items: center;
   // justify-content: space-between;
   flex-direction: column;

   &-logo {
      margin-top: 100px;
      h1 {
         font-style: normal;
         font-weight: bold;
         font-size: 44px;
         line-height: 56px;
         display: flex;
         align-items: center;
         /* or 127% */
         
         letter-spacing: -0.5px;
         
         color: #0A0A0A;

         span {
            color: #FFB00B;
            background: #0A0A0A;
            padding: 10px;
            box-sizing: border-box;
            border-radius: 50%;
         }
      }
   }
   
   &-box {
      margin-top: 150px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      padding: 0 15px;
      box-sizing: border-box;
      a {
         width: 100%;
         display: block;
         text-decoration: none;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 26px;
         text-align: center;
         letter-spacing: -0.5px;
         margin-top: 20px;
         padding: 10px 20px;
         border: 2px solid transparent;
         
         
         &:nth-child(1) {
            
            color: #0A0A0A;
            background: #FFB00B;
            box-shadow: 0px 4px 8px rgba(142, 142, 147, 0.25);
            border-radius: 12px;
            &:hover {
               border: 2px solid #0A0A0A;
               background: white;
            }
         }
         &:nth-child(2) {
            
            background: #0A0A0A;
            box-shadow: 0px 4px 8px rgba(142, 142, 147, 0.25);
            border-radius: 12px;
            color: #FFB00B;
            &:hover {
               background: white;
               border: 2px solid #0A0A0A;
            }
         }
         
      }
   }

   &-footer {
      position: absolute;
      bottom: 0;
      padding: 10px 0;
      a {
         color: #0A0A0A;
         font-size: 20px;

      }
   }
}

