@font-face {
   font-family: 'SF UI Display';
   src: url('../fonts/SFUIDisplay-Medium.eot');
   src: url('../fonts/SFUIDisplay-Medium.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Medium.woff') format('woff'),
      url('../fonts/SFUIDisplay-Medium.ttf') format('truetype');
   font-weight: 500;
   font-style: normal;
   font-display: swap;
}

@font-face {
   font-family: 'SF UI Display';
   src: url('../fonts/SFUIDisplay-Light.eot');
   src: url('../fonts/SFUIDisplay-Light.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Light.woff') format('woff'),
      url('../fonts/SFUIDisplay-Light.ttf') format('truetype');
   font-weight: 300;
   font-style: normal;
   font-display: swap;
}



@font-face {
   font-family: 'SF UI Display';
   src: url('../fonts/SFUIDisplay-Regular.eot');
   src: url('../fonts/SFUIDisplay-Regular.eot?#iefix') format('embedded-opentype'),
      url('../fonts/SFUIDisplay-Regular.woff') format('woff'),
      url('../fonts/SFUIDisplay-Regular.ttf') format('truetype');
   font-weight: normal;
   font-style: normal;
   font-display: swap;
}



body {
   margin: 0;
   padding: 0;
   font-family: 'SF UI Display';
}

h1, h2, h3, h4, h5, h6, p {
   margin: 0;
   padding: 0;
}

.container {
   max-width: 576px;
   width: 100%;
   padding: 0 15px;
   margin: 0 auto;
}

html {
   height: 100%;
}

input[type=number] {
   -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
   -webkit-appearance: none;
   margin: 0;
}
