.block {
   display: flex;
   flex-direction: column;
   width: 100%;
   background: white;
   border-radius: 10px;
   padding: 15px 20px;
   box-sizing: border-box;
   margin-top: 20px;

   &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      
      &:nth-child(2) {
         margin-top: 10px;
      }

      h3 {
         font-style: normal;
         font-weight: bold;
         font-size: 18px;
         line-height: 16px;
         letter-spacing: -0.5px;
         color: #0A0A0A;
      }

      a {
         font-style: normal;
         font-weight: 600;
         font-size: 14px;
         line-height: 16px;
         letter-spacing: 0.5px;
         color: #FFB00B;
         display: flex;
         align-items: center;
         text-decoration: none;
         span {
            
            margin-left: 5px;
         }
      }

      h4 {
         font-style: normal;
         font-weight: bold;
         font-size: 14px;
         line-height: 16px;
         letter-spacing: -0.5px;
         color: #0A0A0A;
      }

      h5 {
         font-style: normal;
         font-weight: 500;
         font-size: 14px;
         line-height: 16px;
         letter-spacing: -0.5px;

         color: rgba(142, 142, 147, 0.8);

      }
   }
}
